.button {
  border: none;
  background-color: transparent;
  padding: 12px 16px;
  font-size: 16px;
  border-radius: 8px;
  transition: all 0.5s;
  cursor: pointer;

  &--add {
    background-color: var(--orange);

    &:hover {
      background-color: var(--orange-hover);
    }
  }
}
